import React from "react"

import {default as parseStrToHtml} from 'html-react-parser';

import YMLFeats from "../core-rules/feats/feats.yml"
import NavLayout from '../layouts/nav-layout';

import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  featsContainer: {
    fontSize: '16px'
  }
})

const FeatsPage = () => {
    const classes = useStyles();

    return (
      <NavLayout >
      <div className={classes.featsContainer} style={{ marginLeft: `24px`}}>
        {YMLFeats.map((data, index) => {
          return (
            <div className='feats-section'>
              <h2>{data.name}</h2>
              <p><strong>Cost: </strong>{data.cost} point</p>
              <p><strong>Prerequisites</strong></p>
              <ul>
                {/* Check old site for parsing */}
                {
                  Object.entries(data.prerequisites).map((item, index) => {
                    // console.log('Items',item)
                    return <li>{item[0]}{Object.values(item[1]).map(value => {
                      // console.log('Vallue', value)
                      if( typeof value === Array) return value[0];
                      // return value.map(attr => {

                      // })
                    })}</li>
                  })
                }
              </ul>
              <p>{data.power}</p>
              <p><strong>Description:</strong></p>
              <p>{data.attackAttributes}</p>
              <p><strong>Effect</strong></p>
              <p>{data.effect} </p>
              <p><strong>Special</strong></p>
              <p>{data.special}</p>
              <Divider /> 
            </div>
          )
        })
        }
      </div>
      </NavLayout>

    )
}
export default FeatsPage;